import Nav from './Nav';
import './App.css';
import Header from './Header';
import Body from './Body';
import Contact from './Contact';
import { Switch, Route } from 'react-router-dom';
import Design from './Design';
import cowMain from './Images/cow-images/cow-home.png';
import cowAbout from './Images/cow-images/cow-about.png';
import cowMenus from './Images/cow-images/cow-menus.png';
import cowChamps from './Images/cow-images/cow-champions.png';
import foodMain from './Images/food-images/food-home.png';
import foodMob from './Images/food-images/food-home-mob.png';
import foodMobH from './Images/food-images/food-home-mob-hov.png';
import foodSearch from './Images/food-images/food-search.png';
import foodSearchF from './Images/food-images/food-search-filter.png';
import foodSearchMob from './Images/food-images/food-search-mob.png';
import foodSearchMobF from './Images/food-images/food-search-mob-filter.png';
import ScrollToTop from './ScrollToTop';

function App() {

  window.onscroll = function () {
    const myNav = document.querySelector('.nav-container');
    if (window.scrollY > 100) {
      myNav.classList.add('nav-bg');
    } else {
      myNav.classList.remove('nav-bg');
    }
  }

  return (
    <div className="main-container">
      <ScrollToTop>
        <Switch>
          <Route exact path="/">
            <Nav />
            <Header />
            <Body />
            <div id="sFour"></div>
            <Contact />
          </Route>
          <Route exact path="/cow">
            <Design
              title='Cow Website Redesign'
              headText='When applying to new restaurants to work at, I always check out their menu on their website before the interview. I noticed immediately that this website seemed outdated and thought that it would be fun to redesign the site. These are some pictures of the site or you could view it live <a class="text-link" href="https://thecoweatery.com/" >here!</a>'
              subTextOne='This is the homepage redesigned. I decided to trim down the menu options and make sure they didnt spill out into two rows. I did this by making the five seperate menu links into a simple drop down, and then removing the online store and delivery options. I got rid of the slider on top and turned it into a simple collage of images. I put their description in a much nicer looking box and added a couple of the top reviews at the bottom with some pictures. I also gave the page a nicer looking footer that matched the navigation bar up top.'
              subTextTwo="The about page seemed a little bland to me, and I didn't like how the pictures seemed like they were just cramed into the text. I decided to just remove the pictures all together and make the text more readable."
              subTextThree="The menu page was one of the biggest changes I made. Since I worked there I knew that the Cow offered different special every day of the week, but none were displayed anywhere on the website! Working there also gave me great insight into the user experience of the menu section, every morning I would have multiple tables confused trying to navigate the menu and figure out where the breakfast or lunch menu was. I simplified the five menu buttons at the top into one, along with the five options in an easy to find place. I removed the dropdown menu that is currently there and laid out all the options for quick navigation when you're hungry and tired in the morning! I then formatted and condensed all the menu options that were spread across the entire page, trying to recreate what a handheld menu would look like on screen. "
              subTextFour="The final piece was to redesign the Holy Cow Hall of Fame. The restaurant has a challenge where if you can finish their famous Holy Cow burger, you earn a place on our website! I was saddened to see that when I looked at the hall of champions there were no names, dates, or any information at all. A lot of tourists would come through the restaurant and attempt this challenge to leave their mark here forever. I thought that on hover it would be cool if their image showed their name, where they were from, and the date the challenge was completed!"
              imgOne={cowMain}
              imgTwo={cowAbout}
              imgThree={cowMenus}
              imgFour={cowChamps}
            />
          </Route>
          <Route exact path="/food">
            <Design
              title='Food Blog'
              headText='text'
              subTextOne='subtext'
              imgOne={foodMain}
              imgTwo={foodMob}
              imgThree={foodMobH}
              imgFour={foodSearch}
              imgFive={foodSearchMob}
              imgSix={foodSearchMobF}
            />
          </Route>
        </Switch>
      </ScrollToTop>
    </div>
  );
}

export default App;
