import React, { Component } from 'react';
import './Nav.css';
import git from './Images/github.svg';
import resume from './Images/file.svg';
import linkedin from './Images/linkedin-2.svg';

class Nav extends Component {
    constructor(props) {
        super(props);
        this.state = { dropped: false };
        this.iconHoverOn = this.iconHoverOn.bind(this);
        this.iconHoverOff = this.iconHoverOff.bind(this);
        this.onDropClick = this.onDropClick.bind(this);
        this.offDropClick = this.offDropClick.bind(this);
        this.onLinkClick = this.onLinkClick.bind(this);
        this.closeBurger = this.closeBurger.bind(this);
    }
    iconHoverOn(e) {
        e.target.nextSibling.classList.add('show');
    }
    iconHoverOff(e) {
        e.target.nextSibling.classList.remove('show');
    }
    onDropClick(e) {
        e.currentTarget.nextSibling.classList.add('show-drop');
        this.setState({ dropped: true });
    }
    offDropClick(e) {
        e.currentTarget.nextSibling.classList.remove('show-drop');
        this.setState({ dropped: false });
    }
    onLinkClick(link) {
        window.location = link;
    }
    closeBurger(e) {
        this.setState({ dropped: false });
        e.target.parentElement.parentElement.classList.remove('show-drop')
    }

    render() {

        return (
            <div className="nav-container">
                <div className="icon-container">
                    <div className="col-center">
                        <img src={git} alt="" className="icon white-svg" onMouseEnter={this.iconHoverOn} onMouseLeave={this.iconHoverOff} onClick={() => { this.onLinkClick('https://github.com/Bbenedict20') }} />
                        <p className="icon-droptext">Github</p>
                    </div>
                    <div className="col-center">
                        <img src={linkedin} alt="" className="icon" onMouseEnter={this.iconHoverOn} onMouseLeave={this.iconHoverOff} onClick={() => { this.onLinkClick('https://www.linkedin.com/in/bradley-benedict-a19a3321a/') }} />
                        <p className="icon-droptext">LinkedIn</p>
                    </div>
                    <div className="col-center">
                        <img src={resume} alt="" className="icon icon white-svg" onMouseEnter={this.iconHoverOn} onMouseLeave={this.iconHoverOff} />
                        <p className="icon-droptext">Resume</p>
                    </div>
                </div>
                <div className="hamburg" onClick={this.state.dropped ? this.offDropClick : this.onDropClick}>
                    <div className={this.state.dropped ? 'l one-active' : 'l one'}></div>
                    <div className={this.state.dropped ? 'l two-active' : 'l two'}></div>
                    <div className={this.state.dropped ? 'l three-active' : 'l three'}></div>
                </div>
                <div className="dropdown">
                    <a href="#sOne" onClick={this.closeBurger}><p className="drop-text">About</p></a>
                    <a href="#sTwo" onClick={this.closeBurger}><p className="drop-text">Front-end Projects</p></a>
                    <a href="#sThree" onClick={this.closeBurger}><p className="drop-text">Design Concepts</p></a>
                    <a href="#sFour" onClick={this.closeBurger}><p className="drop-text">Contact</p></a>
                </div>
                <div className="link-container">
                    <a href="#sOne">About</a>
                    <a href="#sTwo">Front-end Projects</a>
                    <a href="#sThree">Design Concepts</a>
                    <a href="#sFour">Contact</a>
                </div>
            </div>
        )
    }
}

export default Nav;