import axios from 'axios';
import React, { Component } from 'react'
import './Contact.css';

class Contact extends Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.makeMsg = this.makeMsg.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.state = { status: 'notSent', name: '', email: '', msg: '', phone: '' };
    }
    async handleSubmit(e) {
        e.preventDefault();
        alert('not working yet :)')
        // this.setState({ status: 'sending...' });
        // const { name, email, msg, phone } = e.target.elements;
        // let details = {
        //     name: name.value,
        //     email: email.value,
        //     msg: msg.value,
        //     phone: phone.value
        // };

        // let response = await axios.post('http://localhost:3001/contact', details);
        // this.setState({ status: 'sent', name: '', email: '', msg: '', phone: '' });
        // console.log(response.status);
    }
    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value })
    }
    makeMsg() {
        if (this.state.status === 'notSent') {
            return (
                <button>Send</button>
            )
        } else if (this.state.status === 'sending...') {
            return (
                <h3>Sending...</h3>
            )
        } else {
            return (
                <h3>Message Sent!</h3>
            )
        }
    }
    render() {
        return (
            <div>
                <div className="main-container">
                    <h2>Contact</h2>
                    <form onSubmit={this.handleSubmit} action="" className="contact-box">
                        <div className="reg-flex">
                            <div className="main-container">
                                <label htmlFor="name">Name</label>
                                <input
                                    onChange={this.handleChange}
                                    name="name"
                                    type="text"
                                    id="name"
                                    value={this.state.name} />
                            </div>
                            <div className="main-container">
                                <label htmlFor="phone">Phone</label>
                                <input
                                    value={this.state.phone}
                                    name="phone"
                                    onChange={this.handleChange}
                                    type="tel"
                                    id="phone"
                                    placeholder="000-000-0000"
                                    pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}" />
                            </div>
                        </div>
                        <label htmlFor="email" id="email-l">E-mail</label>
                        <input
                            value={this.state.email}
                            name="email"
                            onChange={this.handleChange}
                            type="email"
                            id="email" />
                        <label htmlFor="msg" id="msg-l">Message</label>
                        <textarea
                            value={this.state.msg}
                            onChange={this.handleChange}
                            name="msg"
                            id="msg"
                            cols="30"
                            rows="8"></textarea>
                        {this.makeMsg()}
                    </form>
                </div>
            </div>
        )
    }
}

export default Contact;