import React, { Component } from 'react'
import './Card.css'
import { Link } from 'react-router-dom';


class Card extends Component {
    constructor(props) {
        super(props);
        this.onCodeClick = this.onCodeClick.bind(this);
        this.onLiveClick = this.onLiveClick.bind(this);
    }
    onCodeClick() {
        window.open(this.props.codeURL);
    }
    onLiveClick() {
        window.open(this.props.liveURL);
    }
    render() {
        if (this.props.type === 'dev') {
            return (
                <div className="main-container">
                    <h3 className="card-title">{this.props.title}</h3>
                    <div className="card dev">
                        <img className="card-img" src={this.props.img} alt="" />
                        <p>{this.props.cardText}</p>
                        <div className="button-contain">
                            <button onClick={this.onLiveClick}>See Live!</button>
                            <button onClick={this.onCodeClick}>See Code</button>
                        </div>
                    </div>
                </div>
            )
        } else {
            return (
                <div className="main-container">
                    <h3 className="card-title">{this.props.title}</h3>
                    <div className="card des">
                        <img className="card-img" src={this.props.img} alt="" />
                        <p>{this.props.cardText}</p>
                        <Link to={this.props.link}>
                            <button>Read more!</button>
                        </Link>
                    </div>
                </div>
            )
        }
    }
}

export default Card;